.CharacterBook{
  display: inline-block;
    position: absolute;
    width: 27%;
    height: 60%;
    top: 10%;
    left: 9%;
  /* background-color: red; */
  z-index: 5;
overflow: scroll;
}

.CharacterBook h3{
  text-align: center;
  text-decoration: underline;
}


.CharacterBook_Image{
  position: absolute;
  width: 55%;
  top: -1%;
  left: -15%;
  z-index: 4;
}


.CharacterBook .CharLink{
  /* background-color: blue; */
  display: block;
  margin: 10px;
  width: 40%;
  position: relative;
  top: 10px;
  left: 5%;
}

.CharacterBook_button{
  position: relative;
  margin: 10px;
  top: -56px;
  left: 10%;
  z-index: 5;
}



.CharacterBook_Back,.CharacterBook_Next{
  cursor: pointer;
  background-repeat: no-repeat;
  background-size: contain;
  display: inline-block;
  position: absolute;
  top: 68%;
  width: 50px;
  height: 50px;
  z-index: 4;
  cursor: pointer;
}

.CharacterBook_Back{
background-image: url('../sprites/Arrow.png');
  /* left: 2%; */
    left: 8%;
}




.CharacterBook_Next{
background-image: url('../sprites/ArrowFlip.png');
    /* left: 35%; */
    left: 33%;
}
